* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

*::after,
*::before {
    box-sizing: border-box;
}

body {
    overflow: hidden;
}

.page {
    display: flex;
    flex-wrap: wrap;
}

.page>section {
    height: 40vh;
    width: 50vw;
    border: 1px solid gray;
}